export const validatoinSchemaMethodPayPal = yup.object().shape({
  cashEmail: yup.string()
    .matches(emailRegex, "  email")
    .required('   '),
});

export const validatoinSchemaMethodCard = yup.object().shape({
  card: yup.string()
    .matches(cardRegex, " ")
    .required('   '),
  cardDate: yup.string()
    .matches(cardDateRegex, " ")
    .required('   '),
  cardCVV: yup.string()
    .matches(cardCVVRegex, " ")
    .required('   '),
});